@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 26px;
  font-family: "Roboto Slab", sans-serif;
}

/* Navbar */

nav.bochaperNav {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 10px 40px;
  border-bottom: 2px solid #333;
}
.bochaperNav__logo {
  max-height: 60px;
  margin-right: auto;
}
a.bochaperNav__link {
  font-size: 18px;
  font-weight: 600;
  padding: 20px 30px;
  margin-left: 10px;
  text-decoration: none;
  color: #333;
  border: 2px solid #333;
}
div.Tabs__tab {
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.bochaperMain {
  padding: 40px;
}

div.Tabs {
  padding: 40px;
}
ul.Tabs__nav {
  display: flex;
  justify-content: center;
  list-style: none;
}
li.Tabs__nav-link {
  font-size: 24px;
  padding: 20px 40px;
  background-color: #eee;
  border-radius: 9px;
  margin: 0 10px;
  cursor: pointer;
}
li.Tabs__nav-link:hover {
  opacity: 0.6;
}

audio {
  display: none;
}
.Music {
  padding: 40px;
  display: grid;
  place-items: center;
}
.Music__amogus {
  cursor: pointer;
  max-height: 300px;
}
.Music__amogus:hover {
  opacity: 0.6;
}

footer.footer {
  padding: 40px 80px;
  background: #888;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
div.footer__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
ul.footer__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
}
li.footer__list-item {
  padding: 10px 20px;
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
}
li.footer__list-item a {
  color: #fff;
}
div.footer__credits {
  padding: 20px;
  font-size: 16px;
}

